.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  width: 100%;
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
  background-color: #fff; }

.game_panel {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  width: 1000px;
  height: 420px;
  border: 1px solid;
  background: url("/img/pickup_bg.jpg") no-repeat center;
  background-size: 100%; }
  .game_panel ul.game_info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    padding: 0.5rem;
    height: 1.5rem;
    background: rgba(0, 0, 0, 0.5);
    color: #fff; }
  .game_panel .pickupBlock {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%; }
  .game_panel .btn_outer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 80px; }
  .game_panel .display {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    flex: 1; }
  .game_panel .card_obj {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center; }
    .game_panel .card_obj .card {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      width: 80px;
      height: 80px;
      border-radius: 4px;
      background: rgba(0, 0, 0, 0.8);
      color: #fff; }

button.pickup {
  border: none;
  border-radius: 6px;
  min-width: 120px;
  height: 54px;
  font-size: 1.2rem;
  margin: 0 1rem;
  color: #fff;
  background-color: #0088ff;
  cursor: pointer; }

.test {
  box-sizing: border-box;
